import JMessage from '@/public/js/jmessage-wxapplet-sdk-1.4.2.min.js';
import {
	hex_md5
} from '@/public/js/md5.js';
import emoji from '@/public/js/emoji.js'; 
import {
	timeAgo 
} from '@/public/js/formatTime.js';
const jim = new JMessage({ 

});
export default {
	data() {
		return {
			showEmoji: false,
			message: '',
			chatBoxHeight: 0,
			id: '',
			JiGuangId: '',
			chatList: [],
			avatar: '',
			consultantAvatar: '',
			now_page: 1,
			page_size: 10,
			total: 0,
			currentList: [],
			historyList: [],
			accoutId: '',
			previewImgList: [],
			api: this.$API,
			ImgList: [],
			hasLoading: true,
			result: "",
		}
	},
	onLoad(option) {
		uni.setNavigationBarTitle({
			title: '用户AAA'
		})
		uni.showToast({
			title: '正在初始化...',
			icon: 'loading',
			duration: 1000
		})
		this.accoutId = uni.getStorageSync('accoutId');
		this.id = option.Id;
		this.JiGuangId = option.JiGuangId;
		this.consultantAvatar = option.consultantAvatar;
		// console.log(option);
		// this.$http.get('/API/User/MyInfo')
		// .then(res=>{
		// this.avatar = this.$API + res.Data.HeadImg;
		this.avatar = uni.getStorageSync('avatar');
		// });
		this.getLogChatList(this.now_page, this.page_size);
		// uni.setNavigationBarTitle({
		// 	title: option.name,
		// })
		this.jim_init();
	},
	onShow() {
		this.jim_init();
	},
	onReady() {
		this.jim_init();
		// this.sendMsg();
	},
	computed: {
		emojiList() {
			return emoji;
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
		uni.showToast({
			title: '加载中',
			icon: 'loading',
			duration: 500
		})
		setTimeout(() => {
			this.getLogChatList(this.now_page + 1, this.page_size);
			uni.stopPullDownRefresh();
		}, 500)
	},
	methods: {
		// 消息记录
		getLogChatList(now_page, page_size) {
			this.$http.get('/API/Consultation/GetLogChatList', {
					pageIndex: now_page,
					pageSize: page_size,
					Id: this.id
				})
				.then(res => {
					this.now_page = res.Data.PageIndex;
					this.page_size = res.Data.PageSize;
					this.total = res.Data.TotalPage;
					this.historyList = res.Data.PageList.reverse();
					this.currentList.unshift.apply(this.currentList, this.historyList)
					// console.log(this.currentList[this.currentList.length-1].CreateDate);
					// let newTime = this.currentList[this.currentList.length-1].CreateDate
					let date = new Date();
					let hour = date.getHours();
					let minute = date.getMinutes();
					if (hour < 10) {
						hour = '0' + hour;
					}
					if (minute < 10) {
						minute = '0' + minute;
					}
					// let timestamp = date.valueOf();
					// console.log(timestamp);
					let result = hour + ':' + minute;
					this.result = result;
					// console.log(result); 
					// this.currentList = this.historyList.concat(this.currentList); 
					for (let i = 0; i < this.historyList.length; i++) {
						if (this.historyList[i].ContentType == 1) {
							this.previewImgList.push(this.historyList[i].Content);
						}
					}
					// console.log(this.previewImgList);
					if (this.historyList.length == 0) {
						uni.showToast({
							title: '暂无更多消息!',
							icon: 'none'
						})
					}
				})
		},
		// 选择图片
		addImg() {
			let that = this;
			let username = 'NervesourceBladder' + uni.getStorageSync('accoutId');
			uni.chooseImage({
				count: 1,
				success: res => {
					let appkey = 'b70a470571c6cc121be728f4';
					let random_str = '022cd9fd995849b58b3ef0e943421ed9';
					let secret = '245cf3e7fc7c75369eeeefc0';
					let timestamp = new Date().getTime();
					let signature = hex_md5('appkey=' + appkey + '&timestamp=' + timestamp + '&random_str=' + random_str + '&key=' +
						secret)
					let username = 'NervesourceBladder' + uni.getStorageSync('accoutId');
					jim.init({
						'appkey': appkey,
						'random_str': random_str,
						'signature': signature,
						'timestamp': timestamp
					}).onSuccess(data => {
						jim.login({
								'username': username,
								'password': 'NervesourceBladder123123'
							}).onSuccess(data2 => {
								let tempFilePaths = res.tempFilePaths[0];
								let appkey = 'b70a470571c6cc121be728f4';
								let target_name = this.JiGuangId;
								// let that = this;
								// let arr = [];
								let temp = '';
								// for (let i = 0; i < res.tempFilePaths.length; i++) {
								uni.uploadFile({
									url: that.$API + '/API/Common/Upload?Type=2',
									filePath: tempFilePaths,
									name: 'file',
									success: res => {
										this.showEmoji = false;
										// console.log(res);
										// console.log(tempFilePaths);
										temp = JSON.parse(res.data).Data.filename;
										// console.log(temp);
										// arr.push(this.$API + '/UploadFile/chat/' + JSON.parse(res.data).Data.filename)
										that.sendImg(target_name, appkey, tempFilePaths, temp);
										this.previewImgList.push(this.$API + '/UploadFile/chat/' + JSON.parse(res.data).Data.filename);
										// // console.log(arr);
									},
									fail: err => {
										// console.log(err);
									},
								});
							})
							.onFail(err => {

							})
					}).onFail(err => {})


				},
				fail: err => {
					// console.log(err);
				}
			})
		},
		// 预览图片
		previewImg(e) {
			let current = e.target.dataset.src;
			uni.previewImage({
				current: current,
				urls: this.previewImgList
			})
		},
		// 发送图片
		sendImg(target_name, appkey, tempFilePaths, temp) {
			let that = this;
			this.chatList.push({
				user: 2,
				avatar: this.avatar,
				image: this.$API + '/UploadFile/chat/' + temp,
			})
			jim.sendSinglePic({
				'target_username': target_name,
				'appkey': appkey,
				'image': tempFilePaths
			}).onSuccess((data, msg) => {
				that.$http.post('/API/Consultation/AddLogChat', {
					Id: that.id,
					Content: temp,
					ContentType: 1
				}).then(res => {})
				//回到底部
				wx.createSelectorQuery().select('#x_chat').boundingClientRect(function(rect) {
					// console.log(rect);
					wx.pageScrollTo({
						scrollTop: rect.height + 200,
						duration: 100
					})
				}).exec();
			}).onFail((err) => {
				// console.log(err);
			})
		},
		showEmojiList() {
			this.showEmoji = !this.showEmoji;
		},
		chooseEmoji(e) {
			this.message += e;
		},
		closeOther() {
			this.showEmoji = false;
		},
		// 初始化极光
		jim_init() {
			let appkey = 'b70a470571c6cc121be728f4';
			let random_str = '022cd9fd995849b58b3ef0e943421ed9';
			let secret = '245cf3e7fc7c75369eeeefc0';
			let timestamp = new Date().getTime();
			let signature = hex_md5('appkey=' + appkey + '&timestamp=' + timestamp + '&random_str=' + random_str + '&key=' +
				secret)
			let username = 'NervesourceBladder' + uni.getStorageSync('accoutId');
			jim.init({
				'appkey': appkey,
				'random_str': random_str,
				'signature': signature,
				'timestamp': timestamp
			}).onSuccess(res => {
				this.jim_login(username);
			}).onFail(err => {})
		},
		// 登录极光
		jim_login(username) {
			jim.login({
				'username': username,
				'password': 'NervesourceBladder123123'
			}).onSuccess(res => {
				this.jim_onMsgReceive();
				this.jim_sendMsg();
				this.hasLoading = false;
				//回到底部
				wx.createSelectorQuery().select('#x_chat').boundingClientRect(function(rect) {
					// console.log(rect);
					wx.pageScrollTo({
						scrollTop: rect.height + 200,
						duration: 100
					})
				}).exec();
			}).onFail(err => {})
		},
		// 发送消息
		jim_sendMsg() {
			if (this.message != '' && this.message != null) {
				this.chatList.push({
					user: 2,
					avatar: this.avatar,
					text: this.message
				})
				let that = this;
				let target_name = this.JiGuangId;
				let msg = this.message;
				this.message = '';
				jim.sendSingleMsg({
					'target_username': target_name,
					'content': msg
				}).onSuccess(res => {
					this.message = '';
					that.$http.post('/API/Consultation/AddLogChat', {
						Id: that.id,
						Content: msg,
						ContentType: 0
					}).then(res => {
						// this.message = '';
					})
				}).onFail(err => {}).onAck(res => {})
				this.message = '';
				this.showEmoji = false;
				//回到底部
				wx.createSelectorQuery().select('#x_chat').boundingClientRect(function(rect) {
					// console.log(rect);
					wx.pageScrollTo({
						scrollTop: rect.height + 200,
						duration: 100
					})
				}).exec();
			} else {
				this.message = '';
			}
		},
		// 聊天消息实时监听
		jim_onMsgReceive() {
			let that = this;
			jim.onMsgReceive(function(data) {
				// console.log(data);
				if (data.messages[0].content.msg_type == "text") {
					that.chatList.push({
						user: 1,
						avatar: that.consultantAvatar,
						text: data.messages[0].content.msg_body.text
					})
				} else {
					jim.getResource({
						'media_id': data.messages[0].content.msg_body.media_id
					}).onSuccess(res => {
						that.chatList.push({
							user: 1,
							avatar: that.consultantAvatar,
							image: res.url
						});
						that.previewImgList.push(res.url);
					}).onFail(err => {})
				}
				that.$http.get('/API/Consultation/SetNewsSee', {
						Id: that.id
					})
					.then(res => {
						wx.createSelectorQuery().select('#x_chat').boundingClientRect(function(rect) {
							// console.log(rect);
							wx.pageScrollTo({
								scrollTop: rect.height + 200,
								duration: 100
							})
						}).exec();
					})
			});
		},
	}
}
